import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import WalletRowContainer from './WalletRowContainer';
import DepositReferenceIdModalContainer from '../Retail/RetailSidePanes/RetailFiatDeposit/DepositReferenceIdModalContainer';
import Spinner from '../common/Spinner/Spinner';

import './WalletRowList.css';

const walletRowListClasses = getBEMClasses('wallet-row-list');

const WalletRowListComponent = (props, context) => {
  if (props.fetching) {
    return <Spinner customClass={walletRowListClasses} />;
  }

  const items = props.products.map(item => {
    return (
      <WalletRowContainer key={item.ProductId} productId={item.ProductId} />
    );
  });

  return (
    <div className={walletRowListClasses()}>
      {items}
      <DepositReferenceIdModalContainer />
    </div>
  );
};

WalletRowListComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default WalletRowListComponent;



